@import url(https://fonts.googleapis.com/css?family=Source+Code+Pro:400,700,900);
html {
  background-color: rgb(197, 255, 236);
}

body {
  margin: 0;
  padding: 0 30vw;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

p, blockquote {
  font-family: 'Source Code Pro', monospace;
  margin: 0;
}

h1,h2,h3,h4,h5,h6 {
  font-family: 'Source Code Pro', monospace;
  padding-bottom: 0;
  -webkit-margin-after: 0em;
          margin-block-end: 0em;
  -webkit-margin-before: 0em;
          margin-block-start: 0em;
  margin: 0;
}

a {
  font-family: 'Source Code Pro', monospace;
  text-decoration: none;
}

blockquote {
  background: #f9f9f9;
  border-left: 10px solid #ccc;
  margin: .5em 10px;
  padding: 0.5em 10px;
  quotes: "\201C""\201D""\2018""\2019";
}

blockquote:before {
  color: #ccc;
  content: open-quote;
  font-size: 4em;
  line-height: 0.1em;
  margin-right: 0.25em;
  vertical-align: -0.4em;
}

blockquote p {
  display: inline;
}

#app-mount {
  display: flex;
  align-items: center;
  height: 100vh;
}

@media (max-width:1500px) {
    body {
      padding: 0 20vw;
    }
}

@media (max-width: 1280px) and (max-height: 800px) {
  #app-mount {
    height: auto;
  }
}

@media (max-width:768px) {
  body {
    padding: 20px 7vw;
  }

  #app-mount {
    height: auto;
  }
}
header {
    display: flex;
    justify-items: center;
    align-items: center;
    margin-bottom: 20px;
}

section {
    margin-bottom: 20px;
}

section h2{
    margin-bottom: 5px;
}

.logo {
   width: 75px;
   height: 75px;
   max-width: 100%;
}

.site-header {
    font-weight: 900;
    margin-left: 20px; 
    font-size: 1.7vw;
}

.projects p {
    margin-bottom: 8px;
}

.footer-item {
    font-weight: bold;
    margin-right: 15px;
}

@media (max-width:1000px) {
    .site-header {
        font-size: 3vw;
    }
}

@media (max-width:500px) {
    .logo {
        width: 50px;
        height: 50px;
        max-width: 100%;
     }

    .site-header {
        margin-left: 10px; 
        font-size: 4vw;
    }
}
